'use strict';

/**
* Remove a directory entry.
*
* @module @stdlib/fs-unlink
*
* @example
* import unlink from '@stdlib/fs-unlink';
*
* function done( error ) {
*     if ( error ) {
*         throw error;
*     }
* }
*
* unlink( './beep/boop.txt', done );
*
* @example
* import { sync as unlinkSync } from '@stdlib/fs-unlink';
*
* var err = unlinkSync( './beep/boop.txt' );
* if ( err instanceof Error ) {
*     throw err;
* }
*/

// MODULES //

import setReadOnly from '@stdlib/utils-define-nonenumerable-read-only-property';
import main from './main.js';
import sync from './sync.js';


// MAIN //

setReadOnly( main, 'sync', sync );


// EXPORTS //

export default main;
